#header .navigation-bar {
  display: flex;
  align-items: center;
}

#header .navigation-bar ul.navigation li.nav-element {
  float: left;
}

#header .navigation-bar ul.navigation li.nav-element a {
  display: block;
  position: relative;
  padding: 10px 15px;
  color: #fff;
  font-family: NunitoSans-Regular, sans-serif;
  font-size: 0.9375em;
  font-weight: 400;
}

#header .navigation-bar ul.navigation li.nav-element a::before {
  position: absolute;
  top: 50%;
  left: 50%;
  color: transparent;
  content: '\2022';
  text-shadow: 0 0 transparent;
  font-size: 2em;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  transform: translateX(-50%);
  pointer-events: none;
}

#header .navigation-bar ul.navigation li.nav-element a:hover::before {
  color: #c3c3c3;
  text-shadow: 15px 0 #c3c3c3, -15px 0 #c3c3c3;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

#header .navigation-bar div.menu-bar ul.menu li.menu-item {
  float: left;
}

#header .navigation-bar div.menu-bar ul.menu li.menu-item a {
  display: block;
  padding: 10px 22px;
  margin: 0 30px 0 15px;
  color: #010101;
  background-color: #fff;
  box-shadow: 0 6px 12px rgba(40, 43, 49, 0.08);
